import React, { Component } from 'react'

export default class Todoing extends Component {
    constructor(){
        super();
        this.state = {
            check: false
        };
    }
    render() {
        var {todo} = this.props;
        var {undo} = this.props;
        return (
            <ul>                
                <div>
                    <p>正在进行  {this.props.todo.length}项</p>
                    {todo.map((item,idx)=>
                        <p key={idx} >
                            <input type="checkbox" checked={this.state.check} onChange={()=>this.props.ad(item,idx)}/>
                            {item}---<button onClick={()=>this.props.del(idx)}>删除</button>
                        </p>)}
                </div>
                <div>
                    <p>已经完成  {this.props.undo.length}项</p>
                    {undo.map((item,idx)=>
                        <p key={idx} >
                            <input type="checkbox" checked onChange={()=>this.props.ad2(item,idx)}/>
                        {item}---<button onClick={()=>this.props.del2(idx)}>删除</button>
                        </p>)}  
                </div>
            </ul>
        )
    }
}