import React, { Component } from 'react'
import Todoinput from './Todoinput'
import Todoing from './Todoing'
import './todo.css';
export default class Todolist extends Component {
    constructor(){
        super();
        var todo = JSON.parse(localStorage.getItem("work")) || [];
        var undo = JSON.parse(localStorage.getItem("unwork")) || [];
        this.state = {
            todo:todo,
            undo:undo
        }
    }
    addItem = (data)=>{
        this.setState({
            todo: [...this.state.todo,data]
        },()=>{
            localStorage.setItem('work',JSON.stringify(this.state.todo))
        })
    }
    delItem = (idx)=>{
        this.setState((state,props)=>{
            return {
                todo: this.state.todo.filter((item,index)=>idx!==index)
            }
        },()=>{
            localStorage.setItem('work', JSON.stringify(this.state.todo))
        })
    }
    adItem = (data,idx) => {
        this.setState({
            undo: [...this.state.undo,data],
            todo: this.state.todo.filter((item,index)=>idx!==index)
        },()=>{
            localStorage.setItem('unwork',JSON.stringify(this.state.undo))
            localStorage.setItem('work', JSON.stringify(this.state.todo))
        })
    }
    adItem2 = (data,idx) => {
        this.setState({
            todo: [...this.state.todo,data],
            undo: this.state.undo.filter((item,index)=>idx!==index)
        },()=>{
            localStorage.setItem('unwork',JSON.stringify(this.state.undo))
            localStorage.setItem('work', JSON.stringify(this.state.todo))
        })
    }
    delItem2 = (idx)=>{
        this.setState((state,props)=>{
            return {
                undo: this.state.undo.filter((item,index)=>idx!==index)
            }
        },()=>{
            localStorage.setItem('unwork', JSON.stringify(this.state.undo))
        })
    }
    render() {
        return (
            <div>
                <Todoinput add={this.addItem} ad={this.adItem} del={this.delItem} del2={this.delItem2} ad2={this.adItem2} todo={this.state.todo} undo={this.state.undo}/>
                <Todoing del={this.delItem} del2={this.delItem2} todo={this.state.todo} undo={this.state.undo} ad={this.adItem} ad2={this.adItem2}/>
            </div>
        )
    }
}